
form input {
  width: 250px;
  font-size: 24px;
  border-radius: 50px;
  padding: 5px 10px;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 10px;
}

.card {
  border: 1px solid #474f7e;
  background-color: #5963c4;
  color: white;
  overflow: hidden;
  padding: 20px;
}
